<script>
import ArrowsOne from '@/assets/lottie/hb1a.json'
import ArrowsTwo from '@/assets/lottie/hb2a.json'
import ArrowsThree from '@/assets/lottie/hb3a.json'
import ArrowsFour from '@/assets/lottie/hb4a.json'
import ArrowsFive from '@/assets/lottie/hb5a.json'
import ArrowsSix from '@/assets/lottie/hb6a.json'
import ArrowsSeven from '@/assets/lottie/hb7a.json'

import { store } from '@/assets/js/store.js'

export default {
  name: "HomeHeader",
  props: {},
  methods: {},
  data() {
    return {
        store, 
        ArrowsOne,
        ArrowsTwo,
        ArrowsThree,
        ArrowsFour,
        ArrowsFive,
        ArrowsSix,
        ArrowsSeven,
    }
  },
  mounted() {
    const lotties = document.querySelectorAll('.lottie-animation-container.shape')
    for (const lottie of lotties) {
        const paused = sessionStorage.getItem("playState") === 'false';
        lottie.style.animationPlayState = paused ? 'running' : 'paused';
    }

    const circles = document.querySelectorAll('.wheel .circle')
    const arrows  = document.querySelectorAll('.wheel .arrow')
    for (const circle of circles) {
        circle.addEventListener("mouseover", () => {
            document.getElementById('sticky').classList.add('hidden')
            const circleNum = circle.id;
            for (const arrow of arrows) {
                if (arrow.classList.contains(circleNum)) {
                    arrow.style.display = "block"
                } else {
                    arrow.style.display = "none"
                }
            }
            for (const circle of circles) {
                if (circle.classList.contains(circleNum)) {
                    circle.classList.add('active')
                    circle.classList.remove('inactive')
                } else {
                    circle.classList.add('inactive')
                    circle.classList.remove('active')
                }
            }

        })

        circle.addEventListener("mouseout", () => {
            document.getElementById('sticky').classList.remove('hidden')
            for (const arrow of arrows) {
                arrow.style.display = "none"
            }
            for (const circle of circles) {
                circle.classList.remove('active')
                circle.classList.remove('inactive')
            }
        })
    }
  },
};
</script>
    
